
import { defineComponent, ref, computed } from 'vue'
import { useRoute } from 'vue-router'

const componentList = [
  {
    groupName: '布局组件',
    groups: [
      { name: '分割线', link: '/component/divider' },
      { name: '栅格', link: '/component/grid' },
      { name: '布局', link: '/component/layout' },
      { name: '间距', link: '/component/space' },
    ],
  },
]

const docList = [
  {
    groupName: '介绍',
    groups: [
      {
        name: 'Ladder UI',
        link: '/docs/introduction',
      },
    ],
  },
  {
    groupName: '快速上手',
    groups: [
      {
        name: '安装',
        link: '/docs/install',
      },
    ],
  },
]

export default defineComponent({
  setup() {
    const route = useRoute()

    const navList = ref([
      {
        name: '文档',
        link: '/docs/introduction',
        sign: 'docs',
      },
      {
        name: '组件',
        link: '/component/divider',
        sign: 'component',
      },
    ])

    const sidebarList = computed(() => {
      if (route.path.includes('component')) {
        return componentList
      } else if (route.path.includes('docs')) {
        return docList
      }

      return []
    })

    const isHomePage = computed((): boolean => route.name === 'homepage')

    const routePath = computed(() => {
      return route.path
    })

    const translate = () => {
      alert('完善中✍️')
    }

    return {
      navList,
      isHomePage,
      routePath,
      sidebarList,
      componentList,
      docList,
      translate,
    }
  },
})
